@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500,600,700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  background: #282828; }

*,
*::after,
*::before {
  box-sizing: border-box; }

.App {
  width: 100vw;
  margin: 0 auto;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center; }

.App-nav {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 15%;
  height: 100vh;
  color: #e6f4f1;
  padding: 20px;
  box-shadow: -10px 0px 10px 1px rgba(0, 0, 0, 0.5); }
  .App-nav__line {
    width: 80%;
    height: 1px;
    background: #e6f4f1;
    margin: 20px 0; }
    @media (max-width: 1601px) {
      .App-nav__line {
        margin: 10px 0; } }
  .App-nav__title {
    text-align: right;
    text-transform: uppercase;
    height: 10%; }
    @media (max-width: 1601px) {
      .App-nav__title {
        margin: 0;
        font-size: 20px; } }
  .App-nav__settings {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 25%; }
    .App-nav__settings span {
      display: flex;
      flex-direction: column;
      text-align: center;
      justify-content: center;
      width: 100%; }
    .App-nav__settings input {
      position: relative;
      -webkit-appearance: none;
      width: 100%;
      height: 50%;
      background: #fa19de;
      background: -webkit-gradient(linear, left top, right top, from(#fa19de), to(#ffb01e));
      background: -webkit-linear-gradient(left, #fa19de 0%, #ffb01e 100%);
      background: linear-gradient(90deg, #fa19de 0%, #ffb01e 100%);
      outline: none;
      opacity: 0.75;
      -webkit-transition: 0.2s;
      -webkit-transition: opacity 0.2s;
      transition: opacity 0.2s;
      margin-bottom: 10px;
      border-radius: 50px; }
    .App-nav__settings input:hover {
      opacity: 1; }
    .App-nav__settings input::-webkit-slider-thumb {
      -webkit-appearance: none;
      appearance: none;
      width: 8%;
      height: 55px;
      border-radius: 50px;
      background: #e6f4f1;
      cursor: pointer; }
    @media (max-width: 1601px) {
      .App-nav__settings {
        height: 35%; }
        .App-nav__settings input {
          height: 30%; }
        .App-nav__settings .App-nav__line {
          margin: 0;
          height: 1px;
          width: calc(100% - 30px) !important; }
        .App-nav__settings span {
          height: auto; } }
  .App-nav .step-counter {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 25px;
    font-weight: 500;
    width: 100%; }
    .App-nav .step-counter p {
      margin: 0 20px 0 0;
      text-align: center; }
  .App-nav__algorithms {
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    width: 100%;
    height: 35%; }
    .App-nav__algorithms button {
      position: relative;
      background: #fa19de;
      background: -webkit-gradient(linear, left top, right top, from(#fa19de), to(#ffb01e));
      background: -webkit-linear-gradient(left, #fa19de 0%, #ffb01e 100%);
      background: linear-gradient(90deg, #fa19de 0%, #ffb01e 100%);
      border: none;
      border-radius: 50px;
      padding: 5% 3%;
      margin: 5px 0;
      color: #282828;
      font-weight: 700;
      font-size: 18px;
      font-family: "Montserrat", sans-serif;
      opacity: 0.75; }
    .App-nav__algorithms button:disabled,
    .App-nav__algorithms button[disabled] {
      opacity: 0.25;
      cursor: wait; }
    .App-nav__algorithms button:hover {
      opacity: 1;
      cursor: pointer; }
      .App-nav__algorithms button:hover:disabled {
        cursor: wait; }

.App-container {
  width: 85%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: row;
  padding: 20px; }

.Container-column {
  margin: 0 1px;
  background: #e6f4f1;
  box-shadow: 10px 10px 15px -5px rgba(0, 0, 0, 0.5); }

